@media only screen and (min-width: 641px) {
    div.main {
        position: relative;
        left: 215px;
        margin-top: 25px;
        width: 605px;
        top: 25px;
    }

    div.main div.wrap.articles {
        float: none;
        position: relative;
        margin-top: 25px;
        width: 605px;
    }
}

div.title {
    margin-bottom: -20px;
    padding: 15px;
    border: 1px solid #d6d6d6;
    box-sizing: border-box;
}

div.title > h1 > a {
    color: #292929;
    font-size: 22px;
    font-weight: bold;
}

#writeArticleButton {
    display: block;
    margin-bottom: 5px;
    padding: 0 10px;
    height: 50px;
    line-height: 46px;
    border: 2px solid #d6d6d6;
    box-sizing: border-box;
    cursor: text;
    color: #a6a6a6;
    font-size: 14px;
    background: #f9f9f9 url(https://everytime.kr/images/new/container.write.background.png) no-repeat right center;
    background-size: 40px 40px;
}

div.articles > article {
    margin-bottom: -1px;
    box-sizing: border-box;
    border: 1px solid #e3e3e3;
    background-color: #fff;
}

div.articles > article > a.article {
    display: block;
    padding: 15px;
}

div.articles > article > div.comments {
    display: none;
}

div.articles > article h2.medium {
    margin-bottom: 5px;
    line-height: 18px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    font-weight: normal;
}

div.articles > article h2 {
    color: #292929;
}

div.articles > article p.small { 
    margin-bottom: 5px;
    max-height: 30px;
    line-height: 15px;
    white-space: normal;
    overflow: hidden;
    color: #a6a6a6;
    font-size: 12px;
}

div.articles > article p {
    word-break: break-all;
}

div.articles > article time.small {
    float: left;
    margin-right: 5px;
    height: 15px;
    line-height: 15px;
    font-size: 11px;
}

div.articles > article time {
    color: #a6a6a6;
    letter-spacing: 0;
}

div.articles > article h3.small {
    float: left;
    max-width: 90px;
    height: 15px;
    line-height: 15px;
    font-size: 11px;
    font-weight: normal;
    letter-spacing: 0;
}

div.articles > article h3 {
    color: #292929;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

div.articles > article ul.status {
    float: right;
}

div.articles > article ul.status li.vote {
    padding-left: 15px;
    color: #c62917;
    background-image: url(https://everytime.kr/images/new/container.articles.vote.png);
}

div.articles > article ul.status li.comment {
    padding-left: 15px;
    color: #0ca5af;
    background-image: url(https://everytime.kr/images/new/container.articles.comment.png);
}

div.articles > article ul.status li {
    float: left;
    margin-left: 8px;
    padding: 0 2px;
    height: 20px;
    color: #a6a6a6;
    font-size: 12px;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 11px 11px;
    cursor: pointer;
}

div.articles > article hr {
    clear: both;
    height: 0;
    border: 0;
}

hr {
    clear: both;
    width: 100%;
    height: 0;
    border: 0;
}

div.articles div.pagination > form.search {
    float: left;
    margin-top: 5px;
    padding: 8px 25px 8px 5px;
    border: 1px solid #a6a6a6;
    border-radius: 3px;
    background: transparent url(	https://everytime.kr/images/new/container.articles.pagination.search.png) no-repeat right 10px center;
    background-size: 16px 16px;
}

div.articles div.pagination > form.search > select {
    float: left;
    margin-right: 5px;
    height: 19px;
    line-height: 19px;
    border: 0;
    color: #737373;
    font-size: 12px;
    background-color: transparent;
}

div.articles div.pagination > form.search > *:focus {
    outline: none;
}

div.articles div.pagination > form.search > input.text {
    height: 19px;
    line-height: 19px;
    border: 0;
    width: 130px;
    color: #292929;
    font-size: 14px;
    background-color: transparent;
}

div.articles div.pagination > a.next {
    float: right;
    margin-left: 0;
    padding: 0 25px 0 10px;
    background-image: url(	https://everytime.kr/images/new/container.articles.pagination.next.png);
    background-position: right 10px center;
}

div.articles div.pagination > a {
    display: block;
    margin-top: 5px;
    margin-left: 5px;
    padding: 0 10px 0 25px;
    height: 35px;
    line-height: 35px;
    border: 1px solid #c62917;
    border-radius: 3px;
    color: #c62917;
    font-size: 14px;
    font-weight: bold;
    background-repeat: no-repeat;
    background-position: 10px center;
    background-size: 10px 10px;
    cursor: pointer;
}